
import shuffle from 'lodash/shuffle'
import random from 'lodash/random'
import { Idiom } from "app/idioms/idiom";

export class Puzzle {
  idiom: Idiom;
  variants: Idiom[];
  miss: Idiom[];
  completed: boolean;


  constructor(idiom: Idiom, variants: Idiom[]){

    this.variants = shuffle(variants);


    this.idiom = idiom ? idiom : this.variants[random(this.variants.length - 1)]

    this.miss = [];

    this.completed = false;

    
  }
}



