import { Component, OnInit } from '@angular/core';
import { IdiomsService } from "app/idioms/idioms.service";
import { Idiom } from "app/idioms/idiom";
import { GameService } from "app/idioms/game.service";
import { Puzzle } from "app/idioms/puzzle";


var id = 0;

@Component({
  selector: 'idio-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.scss']
})
export class GameComponent implements OnInit {


  puzzle: Puzzle 

  constructor(private gameService: 
  GameService) { }

  ngOnInit() {
    this.gameService.puzzle.subscribe( puzzle =>{
      console.log('callback' + ++id )
      this.puzzle = puzzle
    })
  }

  check(idiom) {
    if(this.puzzle.idiom == idiom ){
      this.puzzle.completed = true;
      
    } else {
      this.puzzle.miss.push(idiom)
    }
  }

  next(){
    this.gameService.nextPuzzle()
  }

}



