import { Injectable } from '@angular/core';
import { IdiomsService } from "app/idioms/idioms.service";
import { Subject } from "rxjs/Subject";

import random from 'lodash/random'
import { Puzzle } from "app/idioms/puzzle";

@Injectable()
export class GameService {
  puzzle: Subject<Puzzle>;

  idioms: any = {};

  constructor(
    idiomsService: IdiomsService
  ) { 
    console.log('IdiomsService construct')
    this.puzzle = new Subject()

    idiomsService.getIdioms().subscribe( idioms => {
      idioms.forEach(it => this.idioms[it.id]=it)
      this.nextPuzzle()
      
    })


  }

  nextPuzzle(){
 
    this.puzzle.next(new Puzzle(null, [
      this.getRandom(),
      this.getRandom(),
      this.getRandom(),
    ]))
  }


  getRandom(){
    const allIds = Object.keys(this.idioms);

    const idx =  random( allIds.length - 1);
    return this.idioms[allIds[idx]]
  }

}



